.modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  max-height: 100%;
  overflow-y: auto;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, .45);
}

  .modal__inner {
    max-width: 75%;
    max-height: 100%;
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 5px;
    overflow: hidden;
  }

  .modal__title {
    margin-top: 0;
    margin-bottom: 15px;
    font-size: 30px;
    text-align: center;
  }

  .modal__body img {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .modal__close {
    position: absolute;
    right: 20px;
    top: 20px;
    font-size: 40px;
    line-height: 1;
    text-decoration: none;
    color: #fff;
  }
.post {
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -moz-flex-direction: column;
  -o-flex-direction: column;
  flex-direction: column;
  height: 100%;
  border: 1px solid #d5d5d5;
  border-radius: 5px;
  overflow: hidden;
}

  .post__body {
    flex: 1 0 auto;
    height: 220px;
  }

    .post__body img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

  .post__bottom {
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    justify-content: space-between;
    -ms-align-items: center;
    align-items: center;
    flex: 0 0 auto;
    padding: 20px;
  }

    .post__bottom .buttons-group {
      flex-shrink: 0;
    }

  .post__title {
    min-width: 1px;
  }
.posts-list {
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -o-flex-wrap: wrap;
  flex-wrap: wrap;
  padding: 0;
  margin: 0 -15px;
  list-style: none;
}

  .posts-list__item {
    width: 33.3333333%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 30px;
  }
.social {
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  justify-content: center;
  -o-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

  .social__btn {
    display: block;
    padding: 5px 15px;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    text-decoration: none;
    color: #333;
    cursor: pointer;
  }
.share-modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  max-height: 100%;
  overflow-y: auto;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, .45);
}

  .share-modal__inner {
    max-width: 400px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 5px;
    overflow: hidden;
  }

  .share-modal__title {
    margin-top: 0;
    margin-bottom: 15px;
    font-size: 30px;
    text-align: center;
  }

  .share-modal__field {
    display: block;
    width: 100%;
    height: 40px;
    padding: 0 10px;
    margin-top: 10px;
    font-size: 16px;
    border: 1px solid #ddd;
  }

  .share-modal__close {
    position: absolute;
    right: 20px;
    top: 20px;
    font-size: 40px;
    line-height: 1;
    text-decoration: none;
    color: #fff;
  }
.select {
  display: block;
  width: 300px;
  height: 40px;
  padding: 0 10px;
  margin-bottom: 25px;
  font-size: 16px;
}
